import { GameComponentPresets } from "../utils/gameComponentFactory.js";
import { shuffleCards } from "../utils/help.js";
import { getCardPlaceholderText } from "../utils/cardUtils.js";

/**
 * Sum(BlackJack)GameComponentPresets.strategyGame
 * 
 */
const Sum = {
  name: "Sum",
  data() {
    return {
      title: "BlackJack",
      cardsChg: [],
      arr1: [],
      arr2: [],
      number: 54,
      
      // GameComponentPresets.strategyGame
      // gameManager: 
      // customButtons: 
      // step: 
      // difficulty: 
      // seed: 
    };
  },
  component: [],
  methods: {
    init() {
      // 
      this.arr1.splice(0);
      this.arr2.splice(0);
      
      // 
      if (this.cardsChg.length === 0) {
        this.refillCards();
      } else {
        console.log(':', this.cardsChg.length, '');
      }
      
      // 
      this.hit(this.arr1);
      this.hit(this.arr2);
      this.hit(this.arr1);
      this.hit(this.arr2);
    },
    // 
    hit(arr) {
      // 
      if (this.cardsChg.length === 0) {
        console.log('...');
        this.refillCards();
      }
      
      var currentCard = this.cardsChg.shift();
      var value = currentCard >> 2;
      arr.push({ id: currentCard, value: ++value > 10 ? 10 : value });
      this.gameManager.recordOperation();
    },
    
    // 
    refillCards() {
      // 
      this.cardsChg.splice(0);
      for (let i = 0; i < this.number; i++) {
        this.cardsChg.push(i);
      }
      
      // 
      shuffleCards(this.cardsChg, this.cardsChg.length);
      console.log(':', this.cardsChg.length, '');
    },
    // 
    getScore(player) {
      let score = 0;
      let arr = this["arr" + player];
      let flag = false;
      for (let i = 0; i < arr.length; i++) {
        score += arr[i].value;
        if (arr[i].value === 1) {
          flag = true;
        }
      }
      if (score <= 11 && flag === true) {
        score += 10;
      } else if (score > 21) {
        score = 0;
        player == 2 ? this.gameManager.setLose() : this.gameManager.setWin();
      }
      return score;
    },

    async stepFn() {
      if (this.score1 === this.score2) {
        this.gameManager.setDraw();
      } else if (this.score1 < this.score2) {
        this.hit(this.arr1);
      } else if (this.score1 > this.score2) {
        this.gameManager.setLose();
      }
    },
    // 
    handleHitBtn() {
      this.hit(this.arr2);
    },
    
    /**
     *  - 
     * 
     */
    renderTextView() {
      console.log('\n');
      console.log('             (BlackJack)                 ');
      console.log('');
      console.log(`\n: ${this.step}\n`);
      
      // 
      console.log(': 2121');
      
      // 
      console.log('  ');
      const remainingCards = this.cardsChg ? this.cardsChg.length : 0;
      console.log(`  : ${remainingCards} \n`);
      
      // 1
      console.log('  ');
      const playerScore = this.getScore(1);
      if (this.arr1 && this.arr1.length > 0) {
        const playerCards = this.arr1.map(c => getCardPlaceholderText(c.id)).join(' ');
        console.log(`  ${playerCards}`);
        console.log(`  : ${playerScore}${playerScore > 21 ? '  !' : playerScore === 21 ? '  BlackJack!' : ''}\n`);
      } else {
        console.log('  ()\n');
      }
      
      // 2
      console.log('  ');
      const dealerScore = this.getScore(2);
      if (this.arr2 && this.arr2.length > 0) {
        const dealerCards = this.arr2.map(c => getCardPlaceholderText(c.id)).join(' ');
        console.log(`  ${dealerCards}`);
        console.log(`  : ${dealerScore}${dealerScore > 21 ? '  !' : ''}\n`);
      } else {
        console.log('  ()\n');
      }
      
      return '';
    },
    
    /**
     * 
     * 
     */
    getAvailableActions() {
      const actions = [];
      
      // 
      actions.push({
        id: 1,
        label: ' (RESTART)',
        method: 'goon',
        args: []
      });
      
      // 1
      const playerScore = this.getScore(1);
      if (playerScore < 21) {
        actions.push({
          id: 2,
          label: ' (HIT)',
          method: 'handleHitBtn',
          args: []
        });
      }
      
      // 
      actions.push({
        id: 3,
        label: ' (PASS)',
        method: 'pass',
        args: []
      });
      
      return actions;
    },
    
    // 
    // stand() {
    //   // >=17
    //   while (this.getScore(2) < 17) {
    //     this.hit(this.cardsChg, this.arr2);
    //   }
      
    //   // 
    //   const playerScore = this.getScore(1);
    //   const dealerScore = this.getScore(2);
      
    //   if (playerScore > 21) {
    //     this.gameManager.setLose();
    //   } else if (dealerScore > 21) {
    //     this.gameManager.setWin();
    //   } else if (playerScore > dealerScore) {
    //     this.gameManager.setWin();
    //   } else if (playerScore < dealerScore) {
    //     this.gameManager.setLose();
    //   } else {
    //     this.gameManager.setDraw();
    //   }
      
    //   this.gameManager.recordOperation();
    // },
  },
  
  // GameComponentPresets.strategyGame
  // wait(): Promise<void> - 
  // undo(): void - 
  // pass(): void - 
  // goon(): void - 
  // goBack(): void - 
  // step(fn: Function): Promise<void> - 
  // executeMethodWithRenderToString(method: string, ...args: any[]): Promise<void> - 
  // getActions(): Array<Object> - 
  computed: {
    // 
    score1: function () {
      return this.getScore(1);
    },
    score2: function () {
      return this.getScore(2);
    },
  },
};

// Sum
export default GameComponentPresets.strategyGame(Sum, 1000);

