/**
 * 
 * 
 */
export default class GameStateManager {
  constructor(options = {}) {
    // 
    this.hitflag = true; // 
    this.winflag = false; // 
    this.loseflag = false; // 
    this.drawflag = false; // 

    // 
    this.history = [];

    // 
    this.isAutoRunning = false;
    this.autoInterval = null;
    this.autoStepDelay = options.autoStepDelay / 10 || 500; // 500ms10
    this.maxSteps = options.maxSteps || 400; // 400

    // 
    this._listeners = {};
  }

  /**
   * 
   */
  init() {
    this.hitflag = true;
    this.winflag = false;
    this.loseflag = false;
    this.drawflag = false;
    this.history = [];
    this.stopAuto();
    this.emit("init");
  }

  /**
   * 
   * @param {Object} operation - 
   */
  recordOperation(operation, backword = false) {
    backword ? this.history.unshift(operation) : this.history.push(operation);
    this.emit("historyUpdate", this.history);
  }

  /**
   * 
   * @returns {Object|null} null
   */
  undo() {
    if (this.history.length === 0) {
      return null;
    }

    const operation = this.history.pop();
    this.emit("undo", operation);
    this.emit("historyUpdate", this.history);

    // 
    this.winflag = false;
    this.loseflag = false;
    this.drawflag = false;

    return operation;
  }

  /**
   * 
   * @returns {number} 
   */
  getStepCount() {
    return this.history.length;
  }

  /**
   * 
   * @returns {boolean} 
   */
  canUndo() {
    return this.history.length > 0 && this.hitflag && !this.isAutoRunning;
  }

  /**
   * 
   * @param {Function} stepCallback - 
   */
  async startAuto(stepCallback) {
    if (this.isAutoRunning || this.winflag || this.loseflag || this.drawflag) {
      console.log(
        `startAuto: isAutoRunning=${this.isAutoRunning}, win=${this.winflag}, lose=${this.loseflag}, draw=${this.drawflag}`,
      );
      return;
    }

    console.log("...");
    this.isAutoRunning = true;
    this.emit("autoStart");

    let stepCount = 0;

    try {
      while (
        this.isAutoRunning &&
        !this.overflag &&
        stepCount < this.maxSteps
      ) {
        await stepCallback();
        stepCount++;

        await this.wait();
      }

      if (stepCount >= this.maxSteps) {
        console.log(`${this.maxSteps}`);
        this.stopAuto();
      }
    } catch (error) {
      console.error("Auto mode error:", error);
    } finally {
      console.log(`${stepCount}`);
      this.stopAuto();
    }
  }

  /**
   * 
   */
  stopAuto() {
    if (!this.isAutoRunning) {
      return;
    }

    this.isAutoRunning = false;
    if (this.autoInterval) {
      clearInterval(this.autoInterval);
      this.autoInterval = null;
    }

    this.emit("autoStop");
  }

  /**
   * 
   * @param {number} delay - 
   */
  setAutoStepDelay(delay) {
    this.autoStepDelay = delay;
    this.emit("delayChanged", delay);
  }

  /**
   * 
   * @returns {number} 
   */
  getAutoStepDelay() {
    return this.autoStepDelay;
  }

  /**
   * 
   * @param {number} delay - nullautoStepDelay
   * @returns {Promise} Promise
   */
  async wait(delay = null) {
    const actualDelay = delay !== null ? delay : this.autoStepDelay;
    
    // 
    this.emit("beforeWait", actualDelay);
    
    // 
    return new Promise(resolve => setTimeout(resolve, actualDelay));
  }

  /**
   * 
   * @param {Function} stepCallback - 
   */
  async step(stepCallback) {
    if (!this.hitflag || this.overflag) {
      return;
    }

    this.hitflag = false;
    try {
      await stepCallback();
    } catch (error) {
      console.error("Step error:", error);
    } finally {
      this.hitflag = true;
    }
  }

  /**
   * 
   */
  setWin() {
    this.winflag = true;
    this.stopAuto();
    this.emit("win");
  }

  /**
   * 
   */
  setLose() {
    this.loseflag = true;
    this.stopAuto();
    this.emit("lose");
  }

  /**
   * 
   */
  setDraw() {
    this.drawflag = true;
    this.stopAuto();
    this.emit("draw");
  }

  /**
   * 
   * @param {Function} resetCallback - 
   */
  reset(resetCallback) {
    this.stopAuto();
    this.init();
    if (resetCallback) {
      resetCallback();
    }
    this.emit("reset");
  }

  /**
   * 
   * @param {string} event - 
   * @param {Function} listener - 
   */
  on(event, listener) {
    if (!this._listeners[event]) {
      this._listeners[event] = [];
    }
    this._listeners[event].push(listener);
  }

  /**
   * 
   * @param {string} event - 
   * @param {Function} listener - 
   */
  off(event, listener) {
    if (!this._listeners[event]) {
      return;
    }
    const index = this._listeners[event].indexOf(listener);
    if (index > -1) {
      this._listeners[event].splice(index, 1);
    }
  }

  /**
   * 
   * @param {string} event - 
   * @param {*} data - 
   */
  emit(event, data) {
    if (!this._listeners[event]) {
      return;
    }
    this._listeners[event].forEach((listener) => {
      try {
        listener(data);
      } catch (error) {
        console.error(`Error in event listener for ${event}:`, error);
      }
    });
  }

  /**
   * 
   * @returns {Object} 
   */
  getState() {
    return {
      hitflag: this.hitflag,
      winflag: this.winflag,
      loseflag: this.loseflag,
      drawflag: this.drawflag,
      step: this.getStepCount(),
      isAutoRunning: this.isAutoRunning,
    };
  }

  /**
   * 
   * @param {Object} state - 
   */
  setState(state) {
    Object.assign(this, state);
    this.emit("stateChange", this.getState());
  }

  /**
   * 
   * Vue
   * @returns {Object} 
   */
  static getDefaultComputedProperties() {
    return {
      // 
      hitflag() {
        const manager = this.gameManager;
        return manager ? manager.hitflag : true;
      },

      overflag() {
        const manager = this.gameManager;
        return manager ? manager.overflag : false;
      },

      winflag() {
        const manager = this.gameManager;
        return manager ? manager.winflag : false;
      },

      loseflag() {
        const manager = this.gameManager;
        return manager ? manager.loseflag : false;
      },

      drawflag() {
        const manager = this.gameManager;
        return manager ? manager.drawflag : false;
      },

      // 
      step() {
        const manager = this.gameManager;
        return manager ? manager.getStepCount() : 0;
      },

      // 
      undoDisabled() {
        const manager = this.gameManager;
        return !manager || !manager.canUndo();
      },

      restartDisabled() {
        const manager = this.gameManager;
        return !manager || !manager.hitflag || manager.isAutoRunning;
      },

      stepDisabled() {
        const manager = this.gameManager;
        return (
          !manager ||
          !manager.hitflag ||
          manager.isAutoRunning ||
          manager.overflag
        );
      },

      autoDisabled() {
        const manager = this.gameManager;
        return (
          !manager ||
          !manager.hitflag ||
          manager.overflag
        );
      },

      // 
      canOperate() {
        const manager = this.gameManager;
        return manager && manager.hitflag && !manager.isAutoRunning;
      },

      // 
      gameState() {
        const manager = this.gameManager;
        return manager ? manager.getState() : {};
      },
    };
  }
}
